<template>
<div>
    <div v-if="isLoading" class="center pt-5">
        <div class="spinner-border text-primary" role="status">
            <span class="sr-only">Cargando contenido...</span>
        </div>
    </div>
   <DeptoBase v-else
    :goal="almacen.meta_semana"
    :progress="almacen.progress"
    :name="almacen.nombre"
    :depto="almacen.depto"
    :days="almacen.dias_laborados"
    :m3="almacen.m3_persona"
    :bonus_p="almacen.bono_persona"
    :bonus_tot="almacen.bono_depto"
    :asis="almacen.asistencia"
    :extra="almacen.$_extra_m3"
    :bonus_prod="almacen.bono_productividad"
    :bonus_goals="almacen.bono_metas"
    />
</div>
</template>


<script>
import DeptoBase from '@/components/DeptoBase';

export default {
    name: 'Almacen',
    components: {DeptoBase},
    created() {
        this.$store.dispatch("plants/getInfo", {
            department: 'almacen',
            userCode: this.$route.params.user,
            city: 'monterrey'
        }); 
    },
    computed: {
        isLoading() {
            return this.$store.getters["plants/loading"];
        },
        almacen() {
        return this.$store.getters["plants/data"];
        }
    }
}
</script>